import React from 'react';
import { BrowserRouter as Router, HashRouter, Routes, Route } from 'react-router-dom';
import Home from './pages/Home';
import SignUp from './components/SignUp';
import NavBar from './components/NavBar';
import styles from './App.module.css';
import LoginForm from './components/LoginForm';
import ActorRegistration from './components/ActorRegistration';

const App = () => {
  console.log('Rendering App.js');

  // Check if running in development mode (local URL)
  const isDevelopment = process.env.NODE_ENV === 'development';

  return (
    <div className={styles.appContainer}>
      {/* Use BrowserRouter for local development and HashRouter for GitHub Pages */}
      {isDevelopment ? (
        <Router>
          <NavBar />
          <Routes>
            <Route path="/register" element={<SignUp />} />
            <Route path="/login" element={<LoginForm />} />
            <Route path="/register1" element={<ActorRegistration />} />
            <Route path="/" element={<Home />} />
          </Routes>
        </Router>
      ) : (
        <HashRouter basename="/forscreens_frontend">
          <NavBar />
          <Routes>
            <Route path="/register" element={<SignUp />} />
            <Route path="/login" element={<LoginForm />} />
            <Route path="/register1" element={<ActorRegistration />} />
            <Route path="/" element={<Home />} />
          </Routes>
        </HashRouter>
      )}
    </div>
  );
};

export default App;
